<template>
    <p-toolbar class="p-p-1">
        <template #left>
            <p-button
                label="Save"
                class="p-mr-4"
                @click="save"
                v-if="hasUpdatePermission || hasCreatePermission"
            />

            <p-divider layout="vertical" />
            <span>
                FSH:
                <strong
                    :style="
                        isStyleRed('FSH', bloodTests.fsh)
                            ? { color: 'red' }
                            : {}
                    "
                    >{{ bloodTests.fsh }}</strong
                >
            </span>
            <p-divider layout="vertical" />
            <span>
                TSH:
                <strong
                    :style="
                        isStyleRed('TSH', bloodTests.tsh)
                            ? { color: 'red' }
                            : {}
                    "
                    >{{ bloodTests.tsh }}</strong
                >
            </span>
            <p-divider layout="vertical" />
            <span>
                PRL:
                <strong
                    :style="
                        isStyleRed('PRL', bloodTests.prl)
                            ? { color: 'red' }
                            : {}
                    "
                    >{{ bloodTests.prl }}</strong
                >
            </span>
            <p-divider layout="vertical" />
            <span>
                BhCG:
                <strong
                    :style="
                        isStyleRed('BHCG', bloodTests.bhcg)
                            ? { color: 'red' }
                            : {}
                    "
                    >{{ bloodTests.bhcg }}</strong
                >
            </span>
            <p-divider layout="vertical" />
            <span>
                TESTO: <strong>{{ bloodTests.testo }}</strong>
            </span>
            <p-divider layout="vertical" />
            <span>
                DHEAS:
                <strong
                    :style="
                        isStyleRed('DHEAS', bloodTests.dheas)
                            ? { color: 'red' }
                            : {}
                    "
                    >{{ bloodTests.dheas }}</strong
                >
            </span>
            <p-divider layout="vertical" />
            <span>
                AMHP: <strong>{{ bloodTests.amh }}</strong>
            </span>
        </template>

        <template #right>
            <p-button
                label="Cancel Message Lab"
                class="p-mr-2 app-button p-button-sm"
            />
            <p-button
                label="Lab Contacts"
                class="app-button p-button-sm"
                @click="openLabContactsDailog"
            />
        </template>
    </p-toolbar>
</template>

<script>
import { emitter } from "@/event-bus";
import { useStore } from "vuex";
import { computed, inject } from "vue";
import { usePermissionCheck } from "@/composable/users";
import { useBloodTestUtil } from "@/composable/blood-test-util";

export default {
    setup() {
        const store = useStore();

        const bloodTests = computed(
            () => store.state.cycleMonitoring.bloodTests
        );

        const labContactsDailog = inject("labContactsDailog");

        const {
            hasUpdatePermission,
            hasCreatePermission
        } = usePermissionCheck();

        const { isStyleRed } = useBloodTestUtil();

        return {
            bloodTests,
            save: () => {
                emitter.emit("cycle-monitoring-toolbar:save");
            },
            openLabContactsDailog: () => {
                labContactsDailog.value = true;
            },
            hasUpdatePermission,
            hasCreatePermission,
            isStyleRed
        };
    }
};
</script>

<style scoped>
strong {
    color: #0025cc;
    font-size: 14px;
}

#cycle-monitoriing .p-toolbar {
    border: 1px solid white;
}

#cycle-monitoriing .p-divider.p-divider-vertical:before {
    border-color: white;
}
</style>
