<template>
    <p-card>
        <template #content>
            <div class="p-fluid p-grid">
                <div class="p-col-12">
                    <div class="p-field p-grid">
                        <label for="next-plans" class="p-col-fixed p-mb-1">
                            Next Plans
                        </label>
                        <div class="p-col-fixed">
                            <p-input-text
                                v-model="form.nextPlan"
                                id="next-plans"
                                type="text"
                            />
                        </div>
                        <label for="consent" class="p-col-fixed p-mb-1">
                            Consent
                        </label>
                        <div class="p-col">
                            <p-input-text
                                v-model="form.consents"
                                id="consent"
                                type="text"
                            />
                        </div>
                        <div class="p-col-12 p-mt-2">
                            <div class="p-field p-grid">                       
                                <label for="other-drug-line2" class="p-col-fixed p-mt-1">
                                    Alert
                                </label>
                                <div class="p-col p-mt-1">
                                    <p-input-text
                                        v-model="form.alertMsg"
                                        id="other-drug-line2"
                                        type="text"
                                        style="color: red;"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </template>
    </p-card>
</template>

<script>
import { computed, inject, reactive, watch } from "vue";
import { useStore } from "vuex";

export default {
    setup() {
        const store = useStore();
        const monitorDetails = computed(
            () => store.state.cycleMonitoring.details
        );

        const summaryForm = inject("summaryForm");
        const form = reactive({
            nextPlan: null,
            consents: null,
            alertMsg: null,
        });

        watch(form, (newValue) => {
            summaryForm.nextPlan = newValue.nextPlan;
            summaryForm.consents = newValue.consents;
            summaryForm.alertMsg = newValue.alertMsg;
        });

        watch(monitorDetails, (newValue) => {
            form.nextPlan = newValue.nextPlan;
            form.consents = newValue.consents;
            form.alertMsg = newValue.alertMsg;
        });

        return { form };
    }
};
</script>
